* {
  box-sizing: border-box; }

html {
  font-family: "SF Pro display", Helvetica, Arial, sans-serif;
  font-size: 15px; }

body {
  min-height: 100vh;
  padding: 0;
  margin: 0; }

main {
  display: block; }

ul,
ol {
  padding: 0;
  margin: 0; }

h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0; }

.page-nav {
  background-color: rgba(45, 45, 45, 0.98);
  display: flex;
  justify-content: center;
  height: 50px;
  padding: 0 8px; }

.page-nav__menu {
  display: flex;
  justify-content: space-between;
  max-width: 960px;
  width: 100%;
  list-style: none; }

.page-nav__link {
  height: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  transition: 200ms; }

.page-nav__link:hover {
  color: #666666;
  transition: 200ms; }

.page-nav__switcher {
  background-color: transparent;
  border: none;
  height: 20px;
  width: 20px;
  outline: none;
  font-size: 1rem;
  position: relative; }

.page-nav__switcher:before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #fff;
  position: absolute;
  top: 5px;
  left: 0;
  transition: all 200ms; }

.page-nav__switcher:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #fff;
  position: absolute;
  bottom: 5px;
  left: 0;
  transition: all 200ms; }

.page-nav.opened .page-nav__switcher:before {
  transform: translateY(5px) rotate(45deg); }

.page-nav.opened .page-nav__switcher:after {
  transform: translateY(-4px) rotate(-45deg); }

.page-nav__controls {
  display: none;
  justify-content: space-between;
  align-items: center;
  width: 100%; }

.page-nav_logo {
  width: 20px;
  height: 100%;
  background-size: 20px 44px;
  background-image: url(./assets/images/apple_image.svg);
  background-repeat: no-repeat;
  background-position: center center; }

.chapter-nav {
  height: 116px;
  padding-top: 12px;
  padding-bottom: 12px;
  display: flex;
  justify-content: center;
  background: rgba(242, 242, 242, 0.7);
  min-width: 768px; }

.chapter-nav__items {
  display: flex;
  justify-content: center;
  height: 100%;
  width: 100%;
  list-style: none;
  vertical-align: top; }

.chapter-nav__item {
  display: inline-block;
  vertical-align: top;
  padding: 0 20px; }

.chapter-nav__link {
  margin-top: 3px;
  display: flex;
  flex-direction: column;
  align-items: top;
  text-align: center;
  color: rgba(0, 0, 0, 0.8);
  text-decoration: none; }

.chapter-nav__link:hover {
  color: #0070c9; }

.chapter-nav__icon {
  height: 54px;
  margin: 0 auto 6px;
  background-repeat: no-repeat; }

.chapter-nav-icon__iphone-x {
  width: 20px;
  background-size: 20px 54px;
  background-image: url(./assets/images/iphone-x.svg); }

.chapter-nav-icon__iphone-8 {
  width: 37px;
  background-size: 37px 54px;
  background-image: url(./assets/images/iphone8_large.svg); }

.chapter-nav-icon__iphone-se {
  width: 18px;
  background-size: 18px 54px;
  background-image: url(./assets/images/iphonese_large.svg); }

.chapter-nav-icon__ios11 {
  width: 32px;
  background-size: 32px 54px;
  background-image: url(./assets/images/iphone_ios_large.svg); }

.chapter-nav-icon__accessories {
  width: 44px;
  background-size: 44px 54px;
  background-image: url(./assets/images/iphone_acc_large.svg); }

.chapter-nav-icon__compare {
  width: 50px;
  background-size: 50px 54px;
  background-image: url(./assets/images/iphone_comp_large.svg); }

.chapter-nav__label {
  font-family: "SF Pro Text", "SF Pro Icons", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  font-size: 11px; }

.chapter-nav__new {
  font-family: "SF Pro Text", "SF Pro Icons", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  font-size: 9px;
  color: #ef5602;
  line-height: 1.22226;
  font-weight: 400;
  letter-spacing: -0.006rem; }

.section {
  text-align: center; }

.section__iphone-x {
  margin-top: 20px;
  background: #f7f7f7;
  text-align: center; }

.section__content {
  padding-top: 70px;
  margin: 0 auto; }

.section__description {
  margin: 0 auto; }

.section__description_content {
  width: 80%;
  margin: 0 auto; }

.section__header_large {
  font-size: 56px; }

.section__header_small {
  font-size: 40px; }

.section__availability {
  color: #888;
  padding-top: 12px;
  font-size: 16px;
  font-weight: 200; }

.section__availability > span {
  padding: 0 16px; }

.section__link {
  display: inline-block;
  padding-top: 12px;
  color: #0070c9;
  font-size: 21px;
  font-weight: 200;
  position: relative;
  text-decoration: none; }

.section__link:after {
  content: ">";
  font-size: 16.5px;
  position: absolute;
  transform: translate(50%, 17%); }

.section__img {
  background-repeat: no-repeat;
  background-position: center; }

.section__iphone-x .section__img {
  background-image: url(./assets/images/iphone_x_large_2x.jpg);
  width: 981px;
  height: 206px;
  background-size: 981px 206px;
  margin: 50px auto;
  display: inline-block; }

.section__iphone-8 {
  margin-top: 20px;
  background: #f9f0eb;
  text-align: center; }

.section__iphone-8 .section__img {
  background-image: url(./assets/images/iphone_8_large_2x.jpg);
  width: 611px;
  height: 498px;
  background-size: 611px 498px;
  margin: 0 auto;
  margin-top: 50px; }

.section__compare-iphones {
  margin-top: 20px;
  background-color: #fafafa; }

.section__compare-iphones .section__content {
  padding-top: 70px; }

.section__grid-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row-reverse;
  height: 100%; }

.section__compare-iphones .section__img {
  background-image: url(./assets/images/compare_large_2x.jpg);
  width: 587px;
  height: 449px;
  background-size: 587px 449px;
  margin: 0 auto; }

@media (max-width: 767px) {
  .page-nav__controls {
    display: flex; }
  .page-nav.opened {
    background-color: black; }
  .page-nav__menu {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100vw;
    min-height: 100vh;
    background-color: black;
    flex-direction: column;
    justify-content: flex-start;
    padding: 0 45px;
    display: flex;
    visibility: hidden;
    transition: all 200ms;
    transform: scaleY(0);
    transform-origin: top; }
  .page-nav.opened .page-nav__menu {
    visibility: visible;
    transform: scaleY(1); }
  .page-nav__link {
    padding: 15px 0;
    border-bottom: 1px solid #292929;
    opacity: 0;
    transition: opacity 150ms 30ms; }
  .page-nav.opened .page-nav__link {
    opacity: 1; }
  .section__content {
    max-width: 336px;
    padding-top: 55px; }
  .section__header_large {
    font-size: 40px; }
  .section__header_small {
    font-size: 35px; }
  .section__availability {
    font-size: 14px; }
  .section__iphone-x {
    margin-top: 12px; }
  .section__iphone-x .section__img {
    background-image: url(./assets/images/iphone_x_small_2x.jpg);
    background-size: 336px 71px;
    width: 336px;
    height: 71px;
    margin-top: 35px; }
  .section__iphone-8 {
    margin-top: 12px; }
  .section__iphone-8 .section__img {
    background-image: url(./assets/images/iphone_8_small_2x.jpg);
    background-size: 321px 229px;
    width: 321px;
    height: 229px;
    margin-top: 35px; }
  .section__compare-iphones .section__content {
    padding-top: 55px; }
  .section__compare-iphones .section__img {
    background-image: url(./assets/images/compare_small_2x.jpg);
    background-size: 303px 275px;
    width: 303px;
    height: 275px;
    margin-top: 35px; } }
